<template>
  <div class="banner">
    <i
      class="fa-regular fa-circle-left"
      @click="shiftLeft"
      :class="{ disabled: bannerIndex === 0 }"
    ></i>
    <img :src="bannerList" />
    <i
      class="fa-regular fa-circle-right"
      @click="shiftRight"
      :class="{ disabled: bannerIndex === 4 }"
    ></i>
  </div>
</template>
<script>
export default {
  data () {
    return {
      banner: [
        'https://media.istockphoto.com/photos/business-owner-at-a-bakery-shop-picture-id588598056?k=20&m=588598056&s=612x612&w=0&h=CzAw591lOyxoMtVE8aKD7mEuXqk90XQpjejsE6wFItk=',
        'https://media.istockphoto.com/photos/interior-design-of-bakery-picture-id514111760?k=20&m=514111760&s=612x612&w=0&h=lh8qMYpAbybmuP1WZQ848Y4QEob45v0C3AopKXOmb4U=',
        'https://media.istockphoto.com/photos/desserts-picture-id91457258?k=20&m=91457258&s=612x612&w=0&h=NEFpzC02Sju4ZjXf0v6npzbN3AqrThma2miSkuain8U=',
        'https://media.istockphoto.com/photos/couple-buying-bread-at-the-bakery-picture-id590149084?k=20&m=590149084&s=612x612&w=0&h=QZhnyV6g3ztfLRbNG207u9BxIlLqa6_Z5_dU029iS44=',
        'https://media.istockphoto.com/photos/cafe-staff-offering-fancy-and-sponge-cakes-picture-id519610636?k=20&m=519610636&s=612x612&w=0&h=ksN3z-DgzDYE-EI6_MdoKrVuCy8yFAzRjmLj_HSbsQs='
      ],
      bannerIndex: 0
    }
  },
  computed: {
    bannerList () {
      return this.banner[this.bannerIndex]
    }
  },
  methods: {
    shiftLeft () {
      if (this.bannerIndex >= 1) {
        this.bannerIndex -= 1
      }
    },
    shiftRight () {
      if (this.bannerIndex < 4) {
        this.bannerIndex += 1
      }
    }
  }
}
</script>
<style lang="scss" scoped>
$font-color-1: #273c75;
$font-color-2: rgba(179, 0, 0, 0.89);
$bg-color-1: #ecf9ff;
$bg-color-2: #fff1c5;
$shadow-1: 1px 2px 2px $font-color-1, -1px -1px 2px #b1b1b1;
$shadow-2: -1px -2px 2px $font-color-1, 1px 1px 2px #b1b1b1;
.banner {
  width: 84%;
  height: 50%;
  margin: 10px auto 0;
  padding: 20px 10px;
  text-align: center;
  background-color: $bg-color-1;
  border-radius: 10px;
  position: relative;
  img {
    width: 880px;
    height: 470px;
    border-radius: 10px;
    box-shadow: $shadow-1;
  }
  i {
    position: absolute;
    font-size: 30px;
    color: $font-color-2;
    font-weight: 900;
    opacity: 0.3;
    transition: all 0.4s;
    &:hover {
      opacity: 0.9;
      cursor: pointer;
      transform: scale(1.2);
    }
    &:hover.disabled {
      opacity: 0.3;
      transform: scale(1);
      cursor: default;
    }
    &:first-child {
      top: 50%;
      left: 25px;
      transform: translate(-25px -50%);
    }
    &:last-child {
      top: 50%;
      right: 25px;
      transform: translate(25px -50%);
    }
  }
}
</style>
